.button { display: inline-block; position: relative; color: #fff; font-size: 14px; border-radius: 0; font-weight: bold; text-align: center; text-decoration: none; text-transform: uppercase; line-height: 20px; cursor: pointer; border-bottom: 1px solid #fff; transition: all 0.3s cubic-bezier(0.3, 0, 0.58, 1); }

.button:before { content: ""; position: absolute; bottom: -1px; left: 0; right: 0; margin: 0 auto; width: 0; border-bottom: 1px solid #fff; transition: width ease-out 350ms; transition-delay: 50ms; }

.button:hover, .button:active { border-bottom: 1px solid transparent; }

.button:hover:before, .button:active:before { width: 100%; }

.button--black { color: #040404; border-bottom: 1px solid #D17D74; }

.button--black:before { border-bottom: 1px solid #D17D74; }

.button-box { display: inline-block; position: relative; min-width: 220px; color: #fff; padding: 14px 15px; font-size: 14px; border-radius: 0; font-weight: bold; text-align: center; text-decoration: none; text-transform: uppercase; overflow: hidden; letter-spacing: .08em; cursor: pointer; border: 2px solid #fff; transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1); }

.button-box:before { content: ''; display: block; position: absolute; top: 0px; left: 0px; height: 100%; width: 0px; background: #fff; transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1); z-index: -1; }

.button-box:after { content: ""; position: absolute; height: 0%; left: 50%; top: 50%; width: 150%; transition: all 0.75s ease 0s; z-index: -1; }

.button-box:hover, .button-box:active { color: #040404; background-color: transparent; }

